<template>
  <div class="header">
    <el-row >
      <el-col :span="18" :offset=3>
        <ul class="nav">
            <li class="nav-item" v-bind:key="index" v-for="(item, index) in newsClassArr">
              <a v-bind:href="'/'+item['content']">{{ item['class_name'] }}</a>
            </li>
        </ul>
        <p>  
      </p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// import newsClass from '../../../newsClass'
export default {
  name: 'pubHeader',
  data () {
    return {
      newsClassArr:'',
      data: ''
    }
  },
mounted: function () {
    // console.log(newsClass) http://127.0.0.1:80/public/
       this.$http.get('/public/index/news_classification').then(response => {
             console.log(response);
            // get body data
            // this.someData = response.body;
            this.newsClassArr=response.body

        }, response => {
            console.log("error");
        });
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.nav{padding: 4px;}
.nav .nav-item{float: left;padding-left: 10px;padding-right: 10px;line-height: 40px;font-size: 16px;}
.nav .nav-item a{color: #333;text-decoration:none;display: block;height: 100%;width: 100%;}
.nav .nav-item a:hover{color: #eb7350;}
</style>



